.title{
    border: 1px solid #000;
    width: 450px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 22px;
    font-weight: 600;
    margin: 10px 0px 5px 7%;
}

.pokemon-block{
    width: 100%;
    display: flex;
}

.pokemon-block-1{
    display: flex;
    width: 430px;
    flex-wrap: wrap;
}


.pokemon-block-item{
    display: flex;
    flex-direction: column;
    align-items: center;
    flex: 0 1 30%;
    height: 170px;
    border: 1px solid #000;
    margin: 5px 0px 0px 8px;
}

.pokemon-block-item-foto img{
    width: 100%;
    height: 100%;
    object-fit: cover;

}

.pokemon-block-item-name{
    font-size: 16px;
    font-weight: 600;
}

.pokemon-block-item-type{
 display: flex;
 width: 100%;

}

.pokemon-block-item-types{
  flex: 0 1 50%;
  margin: 10px 0px 0px 0px;
}

.span-1{
    display: flex;
    padding: 3px;
    margin: 0px 5px 0px 5px;
    border-radius: 5px;
    border: 1px solid #000;  
    justify-content: center;
}


.span-1::first-letter{
font-size: 20px;
color: red;
}

.pokemon-block-item:hover{
 cursor: pointer;
}


.pokemon-block-2{
    width: 300px;
    padding: 8px;
    display: none;
}

.show{
    display: block;
}

.pokemon-block-2-foto img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    border: 1px solid #000;
    padding: 2px;
}

.pokemon-block-2-name{
    display: flex;
    justify-content: center;
    font-size: 25px;
    margin: 5px 0px 5px 0px;
    font-weight: bold;
}

.pokemon-block-2-item{
    padding: 6px ;
    border: 1px solid #000;
}


.pokemon-block-2-info{
    display: flex;
}

.pokemon-block-2-info-name{
    flex: 0 1 65%;
    display: flex;
    justify-content: center;
    border: 1px solid black;
}

.pokemon-block-2-info-name p{
 margin: 3px 0px 3px 0px;
}

.pokemon-block-2-info-skill{
    flex: 0 1 35%;
    display: flex;
    justify-content: center;
     border: 1px solid black;
}

.pokemon-block-2-info-skill p{
    margin: 3px 0px 3px 0px;
}

.btn{
    width: 96%;
    margin: 10px 0px 0px 0px;
    height: 45px;
    border-radius: 8px;
    background-color: #008cff;
    color: #fff;
    font-size: 18px;
}

.btn:hover{
    cursor: pointer;
}

.grass{
    background-color: rgb(98, 185, 98);
}

.fire{
    background-color: rgb(243, 64, 64);
}

.poison{
    background-color: rgb(221, 78, 214);
}

.water{
    background-color: rgb(94, 172, 218);
}

.flying{
    background-color: #fcfc0a;
}

.bug{
    background-color: rgb(169, 238, 162);
}

.normal{
    background-color: rgb(11, 210, 245);
}

.ground{
    background-color: #d3710e;
}

.electric{
    background-color: #969416;
}